import { Component, OnInit } from '@angular/core';
import * as $   from 'jquery' ;
import { TextAnimation } from 'ngx-teximate';
import { fadeInLeft , fadeInRight , fadeInDown , fadeInUp  } from 'ng-animate';



@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.scss']
})
export class AboutComponent implements OnInit {

  enterAnimation: TextAnimation = {
    animation: fadeInDown,
    delay: 50  ,
    type: 'word'
  };

  whiteAnimation: TextAnimation = {
    animation: fadeInDown,
    delay: 50  ,
    type: 'word'
  };

  yellowAnimation: TextAnimation = {
    animation: fadeInRight,
    delay: 50  ,
    type: 'letter'
  };

  darkAnimation: TextAnimation = {
    animation: fadeInUp,
    delay: 50  ,
    type: 'word'
  };

  constructor() { }

  ngOnInit() {
    $(".activeItem").removeClass('activeItem') ;
    $(".aboutSidenav").addClass('activeItem') ;
    $(document).ready(function(){
      setTimeout(()=>{
        setTimeout(function(){
          $(".text_div").fadeIn(0) ;
          $(".animate_all").click() ;
        } , 2000) ;
      } , 600) ;
      setTimeout(()=>{
        $(".one_contact").fadeIn(500) ;
      } , 3500 )
    })
  }

  AnimationsText(table) {
    table.forEach(element => {
      element.enterPlayer.play() ;
    });

  }

}
